<template>
  <div>
    <!--Form +-->
    <el-input placeholder="" v-model="inputSearch" class="input-with-select">
      <!---->
      <!--<el-select v-model="select" slot="prepend" placeholder="">-->
        <!--<el-option label="ID" value="1"></el-option>-->
        <!--<el-option label="" value="2"></el-option>-->
      <!--</el-select>-->
      <!---->
      <!--slot=append-->
      <!--<el-button slot="append"  @click="searchMedicine(inputSearch)" icon="el-icon-search"></el-button>-->
    </el-input>
    <!---->
    <!--reftable
    -->
    <div align="center">
      <el-table
        v-loading="loading"
        element-loading-text=""
        element-loading-spinner="el-icon-loading"
        element-loading-background="rgba(0, 0, 0, 0.8)"
        ref="myTable"
        :data="tables.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @selection-change="handleSelectionChange">
        <!--reserve-selection="true"-->
        <el-table-column
          type="selection"
          reserve-selection>
        </el-table-column>
        <el-table-column
          label="ID">
          <template slot-scope="scope">{{ scope.row.id }}</template>
        </el-table-column>
        <el-table-column
          prop="name"
          label="">
        </el-table-column>
        <el-table-column
          prop="description"
          label=""
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="price"
          label="">
        </el-table-column>
        <el-table-column label="">
          <!--,scope.row
          -->
          <template slot-scope="scope">
            <el-input-number
              size="mini"
              v-model="scope.row.applyNums"
              controls-position="right"
              @change="handleChange()"
              :min="0"
              :max="200">
            </el-input-number>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- -->
    <el-pagination
      small
      @current-change="handleCurrentChange"
      :current-page.sync="currentPage"
      :page-size= "pagesize"
      layout="prev, pager, next, total, jumper"
      :total="drugTable.length">
    </el-pagination>
    <!--<el-button @click="toggleSelection([tableData[1], tableData[2]])"></el-button>-->
    <el-button @click="handleSubmmit()"></el-button>
      <!---->
      <el-dialog title="" :visible.sync="dialogTableVisible">
        <el-table :data="multipleSelection">
          <el-table-column property="id" label="ID" width="150"></el-table-column>
          <el-table-column property="name" label="" width="200"></el-table-column>
          <el-table-column property="applyNums" label="" width="200"></el-table-column>
        </el-table>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogTableVisible = false"> </el-button>
          <!---->
          <el-button type="primary" @click="handleApply();toggleSelection()"></el-button>
        </span>
      </el-dialog>

    <el-button @click="toggleSelection()"></el-button>
    <el-button @click="jumpBack()"></el-button>
  </div>
</template>

<script>
export default {
  name: 'ApplyMedicine',
  data() {
    return {
      // 
      // el-selectvalue
      // label
      select: '',
      // 
      inputSearch: '',
      // 
      currentPage: 1,
      // 
      pagesize:5,
      // total:0,
      tableData: [
        {
          id: '01',
          price: '20.0',
          description: ',',
          name: '',
          applyNums: '0'
        },
        {
          id: '02',
          price: '250.0',
          description: '',
          name: 'wjz',
          applyNums: '0'
        },
        {
          id: '03',
          price: '30.0',
          description: '',
          name: 'xxx',
          applyNums: '0'
        },
        {
          id: '04',
          price: '40.0',
          description: '',
          name: 'xxx',
          applyNums: '0'
        },
        {
          id: '05',
          price: '50.0',
          description: '',
          name: 'xxx',
          applyNums: '0'
        },
        {
          id: '06',
          price: '60.0',
          description: '',
          name: 'xxx',
          applyNums: '0'
        }
      ],
      // 
      multipleSelection: [],
      //
      drugTable:[
        // {
        //   id: '01',
        //   price: '20.0',
        //   description: ',',
        //   name: '',
        //   applyNums: '0'
        // },
        // {
        //   id: '02',
        //   price: '250.0',
        //   description: '',
        //   name: 'wjz',
        //   applyNums: '0'
        // },
        // {
        //   id: '03',
        //   price: '30.0',
        //   description: '',
        //   name: 'xxx',
        //   applyNums: '0'
        // },
        // {
        //   id: '04',
        //   price: '40.0',
        //   description: '',
        //   name: 'xxx',
        //   applyNums: '0'
        // },
        // {
        //   id: '05',
        //   price: '50.0',
        //   description: '',
        //   name: 'xxx',
        //   applyNums: '0'
        // },
        // {
        //   id: '06',
        //   price: '60.0',
        //   description: '',
        //   name: 'xxx',
        //   applyNums: '0'
        // }
      ],
      applyTable:[],
      //
      loading:true,
      dialogTableVisible:false
    }
  },
  computed:{
      // 
      tables (){
        const search = this.inputSearch
        if (search) {
          // filter() 
          //  filter() 
          //  filter() 
          return this.drugTable.filter(data => {
            // some() ;
            // some() 
            // true , ;
            // false
            //  some() 
            //  some() 
            return Object.keys(data).some(key => {
              // indexOf() -1
              // toLowerCase()
              return String(data[key]).toLowerCase().indexOf(search) > -1
            })
          })
        }
        return this.drugTable
      }
  },
  methods: {
    //  @selection-change
    // element-ui 
    // 
    handleSelectionChange (val) {
      this.multipleSelection = val;
    },

    //
    handleChange(){
      //
    },
    //  @size-change
    handleSizeChange (val) {
      this.pagesize = val;
      console.log(` ${val} `);
    },

    //  @current-change
    handleCurrentChange: function() {
      // this.currentPage = val;
      console.log(`:${this.currentPage}`);
      // console.log(`: ${val}`);
      // return this.$axios.get('/allocateCtrl/applyMedicine',{
      //   params:{
      //     index:index
      //   }
      // })
      // .then((res) => {
      //   let data = res.data.data
      //   let status = res.data.status
      //   if (status === 'success') {
      //     this.drugTable = data
      //     // this.total = drugTable.length()
      //     this.total=20
      //   } else if (status === 'fail') {
      //     this.$message.error('')
      //   }
      // })
      // .catch((error) => {
      //   this.$message.error(error.toString())
      // })
    },

    // 
    jumpBack(){
      this.$router.push({path: '/allocateCtrl'})
    },

    // 
    handleSubmmit () {
      if(this.multipleSelection.length != 0)
      {
        for(var i in this.multipleSelection){
          if(this.multipleSelection[i].applyNums === 0){
            this.$message({
              showClose: true,
              message: '0',
              type: 'error'
            });
            break;
          }
          this.dialogTableVisible = true;
        }
      }
      else{
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },

    // Others-Dialog
    handleApply(){
      this.dialogTableVisible = false;
      // map
      // consoledrugTable
      // applyTable,vue
      // http://hcysun.me/vue-design/art/7vue-reactive.html#observer-
      // JSON.stringfy json
      // VO
      this.applyTable = JSON.stringify(
        this.multipleSelection.map(item =>{
          return {drugId:item.id, num:item.applyNums}
        })
      );
      console.log(this.applyTable);
      return this.$axios.post('/allocateCtrl/applyMedicine',{
        applyTable: this.applyTable
      })
      .then((res) => {
        let status = res.data.status;
        console.log(status);
        if (status === 'success') {
          this.$message({
            showClose: true,
            message: '',
            type: 'success'
          })
        } else if (status === 'refused') {
          this.$message.error('')
        }
      })
      .catch((error) => {
        console.log("");
        // TypeError: Cannot read property '$message' of undefined
        this.$message.error("lyfNB")
      })
    },
    // 
    // this.$ref.myTable
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.myTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.myTable.clearSelection();
      }
      //
      for(var i in this.drugTable){
        this.drugTable[i].applyNums = 0;
      }
    }
  },

  // 1method
  // 

  beforeMount:function () {
    return this.$axios.get('/allocateCtrl/showDrugs',{
      params:{
        index: 1
      }
    })
      .then((res) => {
        //res.datares
        let data = res.data.data
        let status = res.data.status
        if (status === 'success') {
          this.drugTable = data
          this.total = data.length
          this.loading = false
        } else if (status === 'fail') {
          this.$message.error('')
        }
      })
      .catch((error) => {
        this.$message.error(error.toString())
      })
  }
}
</script>

<style scoped>
  /**/
  .el-input{
    width: 500px;
  }
  .el-select{
    width: 100px;
  }
  /**/
  .el-table{
    width: 800px;
    height: 400px;
  }
</style>



