<template>
  <div>
    <el-button @click="jumpToApply()"></el-button>
    <el-button @click="jumpToReturn()"></el-button>
  </div>
</template>

<script>
export default {
  name: 'AllocateCtrl',
  methods: {
    jumpToApply () {
      this.$router.push({path: '/applyMedicine'})
    },
    jumpToReturn () {
      this.$router.push({path: '/returnMedicine'})
    }
  }
}
</script>

<style scoped>

</style>



