<template>
  <div>
    <!--Form +-->
    <el-input placeholder="" v-model="inputSearch" class="input-with-select">
      <!---->
      <!--<el-select v-model="select" slot="prepend" placeholder="">-->
      <!--<el-option label="ID" value="1"></el-option>-->
      <!--<el-option label="" value="2"></el-option>-->
      <!--</el-select>-->
      <!---->
      <!--slot=append-->
      <!--<el-button slot="append"  @click="searchMedicine(inputSearch)" icon="el-icon-search"></el-button>-->
    </el-input>
    <!---->
    <!--reftable-->
    <div align="center">
      <el-table
        ref="multipleTable"
        :data="tables.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        @selection-change="handleSelectionChange">
        <!--reserve-selection="true"-->
        <el-table-column
          type="selection"
          reserve-selection>
        </el-table-column>
        <el-table-column
          label="ID">
          <template slot-scope="scope">{{ scope.row.id }}</template>
        </el-table-column>
        <el-table-column
          prop="medName"
          label="">
        </el-table-column>
        <el-table-column
          prop="description"
          label=""
          show-overflow-tooltip>
        </el-table-column>
        <el-table-column
          prop="price"
          label="">
        </el-table-column>
        <el-table-column label="">
          <!--,scope.row
          -->
          <template slot-scope="scope">
            <el-input-number
              size="mini"
              v-model="scope.row.returnNumbers"
              controls-position="right"
              @change="handleChange(scope.row)"
              :min="0"
              :max="200">
            </el-input-number>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!-- -->
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-sizes="[1, 2, 3, 4]"
      :page-size="pagesize"
      layout="total, sizes, prev, pager, next, jumper"
      :total="returnTableData.length">
    </el-pagination>
    <!--<el-button @click="toggleSelection([returnTableData[1], returnTableData[2]])"></el-button>-->
    <el-button @click="handleSubmmit()"></el-button>
    <el-button @click="toggleSelection()"></el-button>
    <el-button @click="jumpTest()"></el-button>
  </div>
</template>

<script>
export default {
  name: 'ApplyMedicine',
  data() {
    return {
      // 
      // el-selectvalue
      // label
      select: '',
      // 
      inputSearch: '',
      // 
      currentPage: 1,
      pagesize: 2,
      returnTableData: [{
        id: '11',
        price: '20.0',
        description: ',',
        medName: '',
        returnNumbers: '0'
      }, {
        id: '12',
        price: '250.0',
        description: '',
        medName: 'wjz',
        returnNumbers: '0'
      }, {
        id: '13',
        price: '50.0',
        description: '',
        medName: 'xxx',
        returnNumbers: '0'
      }],
      // 
      multipleSelection: []
    }
  },
  computed:{
    tables () {
      const search = this.inputSearch
      if (search) {
        // filter() 
        //  filter() 
        //  filter() 
        return this.returnTableData.filter(data => {
          // some() ;
          // some() 
          // true , ;
          // false
          //  some() 
          //  some() 
          return Object.keys(data).some(key => {
            // indexOf() -1
            // toLowerCase()
            return String(data[key]).toLowerCase().indexOf(search) > -1
          })
        })
      }
      return this.returnTableData
    },
  },
  methods: {
    toggleSelection (rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val){
      this.multipleSelection = val;
    },
    handleSubmmit () {
      // console.log(this.applyTable);
      console.log(this.multipleSelection);
    },

    handleChange (row) {
      console.log(row.returnNumbers);
    },
    handleSizeChange (val) {
      this.pagesize = val;
      console.log(` ${val} `);
    },
    handleCurrentChange (val) {
      this.currentPage = val;
      console.log(`: ${val}`);
    },
    searchMedicine (input) {
      console.log(input);
    },
    jumpTest(){
      this.$router.push({path: '/'})
    }
  }
}
</script>

<style scoped>
  /**/
  .el-input{
    width: 500px;
  }
  .el-select{
    width: 100px;
  }
  /**/
  .el-table{
    width: 800px;
    height: 400px;
  }
</style>



